import { Pipe } from '@angular/core';
import { PipeTransform } from '@angular/core/src/change_detection/pipe_transform';
import { bk } from '../../models/bk';

@Pipe({
    name: 'filterbk',
    pure: false
})
export class FilterbkPipe implements PipeTransform{
    
    transform (arr:bk[], id:string):bk[]{
        var bks: bk[];
        var a = [];
        a = arr;
        for(let i=0; i<a.length; i++){
            if(a[i].id == id){
                bks.push(arr[i]);
            }

        }
        console.log(bks);
        return bks;
    }
}


