import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { DataService } from '../leftmenu/share/data.service';
import { bk } from '../models/bk';
import { xe } from '../models/xe';
import { mathang } from '../models/mathang';
import { donhang } from '../models/donhang';
import { khachhang } from '../models/khachhang';

@Component({
  selector: 'app-sua-don-hang',
  templateUrl: './sua-don-hang.component.html',
  styleUrls: ['./sua-don-hang.component.css']
})
export class SuaDonHangComponent implements OnInit {
  donhangs: donhang[];
  donhang: donhang;
  id: string;
  mathang: mathang[];
  xe: xe[];
  bks: bk[];
  bkremove: bk[];
  //item: bk;
  th = "Chọn mặt hàng";
  dt = "";
  sl: number = null;
  dg: number = null;
  statebk = true;
  lengthdh: number;
  t: number = 0;

  state = ["Đã giao","Đang đi giao","Đang bốc hàng", "Chưa giao", "Đơn bị hủy"];
  constructor(private dataService: DataService, private route: ActivatedRoute) {
    this.route.paramMap.subscribe((param: ParamMap) => {
      this.id = param.get('id');
      return this.id;
    });
    this.dataService.getMathangs().subscribe(mh => {
      this.mathang = mh;
      console.log("mh", this.mathang);
    });

    this.dataService.getXes().subscribe(x => {
      this.xe = x;
      console.log("xes", this.xe);
    });

    this.dataService.getDonhangs().subscribe(dh => {
      this.donhangs = dh;

    })

    this.dataService.getBk(this.id).subscribe(bk => {
      this.bks = bk;
     // this.bkremove = bk;
      console.log("viewdonhang-bk", this.bks);
    });
    this.dataService.getBk(this.id).subscribe(bk =>{
      this.bkremove = bk;
    });
  }

  ngOnInit() {


  }
  items = [];
  item = { stt: 1, tenhang: "a", dvt: "v", soluong: 0, dongia: 0, thanhtien: 0 }
  addbk = function (temp) {
    
    if (temp.tenhang != "" && temp.dvt != "" && temp.soluong != 0) {
      this.statebk = true;
      this.item.stt = this.bks.length + 1;
      this.item.tenhang = temp.tenhang;
      this.item.dvt = temp.dvt;
      this.item.soluong = temp.soluong;
      this.item.dongia = temp.dongia;
      this.item.thanhtien = temp.dongia * temp.soluong;
      //this.items.push(this.item);

      temp = [];
      this.th = "Chọn mặt hàng";
      this.dv = "";
      this.sl = null;
      this.dg = null;
    }
    console.log("items", this.items);

    //for (let i = 0; i < this.items.length; i++) {
    var mh: mathang;
    mh = { tenhang: this.item.tenhang, dvt: this.item.dvt, dongia: this.item.dongia };
    var it: bk = { id: this.id, mathang: mh, soluong: this.item.soluong, thanhtien: this.item.thanhtien };

    // this.dataService.addBk(it);
    this.bks.push(it);
    console.log("bks", this.bks);
    //  this.items = [];
    //}


    
    for (let i = 0; i <= this.bks.length; i++) {
      if(this.bks.length==0) {this.t = 0};
      this.t = this.t + this.bks[i].thanhtien;
      
      console.log("tong: ", this.t);
     
      //this.donhang.tong = t;


    }
    for (let i = 0; i < this.donhangs.length; i++) {
      if (this.donhangs[i].id == this.id) {
        this.donhangs[i].tong = this.t;
        console.log("Tong dh: ", this.donhangs[i].tong);
      }
    }
    //  this.dataService.setTong(t,)
  }



  removeBk(index) {
    //console.log(this.bks[index]);
    // var bktemp: bk = this.bks[index];
   
    this.bks.splice(index, 1);
    if(this.bks.length==0) {this.t = 0};
    for (let i = 0; i < this.bks.length; i++) {
      
      this.t = this.t + this.bks[i].thanhtien;
      console.log("tong: ", this.t);
      
    }
    for (let i = 0; i < this.donhangs.length; i++) {
      if (this.donhangs[i].id == this.id) {
        this.donhangs[i].tong = this.t;
        console.log("Tong dh: ", this.donhangs[i].tong);
      }
    }
    //this.dataService.removeBk(bktemp);
    console.log("bangke splice", this.bks);
    console.log("bangke remove", this.bkremove);
  }

  updateBk(ind: string) {
    console.log(ind);
    this.statebk = false;
    this.th = this.bks[ind].mathang.tenhang;
    this.dt = this.bks[ind].mathang.dvt;
    this.sl = this.bks[ind].soluong;
    this.dg = this.bks[ind].mathang.dongia;
    this.removeBk(ind);

  }
  change = function (don) {
    console.log(don);

    if (don.tenkh != "" && don.sdt != "" && don.diachi != "") {
      var i = this.lengthdh + 1;
      var kh: khachhang = { tenkh: don.tenkh, sdt: don.sdt, dc: don.diachi };
      var x: xe;
      for (let i = 0; i < this.xe.length; i++) {
        if (this.xe[i].bienso == don.xe) { x = this.xe[i]; }
      };
      console.log("bkremove", this.bkremove);
      
       
      
      
    
      for (let i = 0; i < this.donhangs.length; i++) {
        console.log("for component", i);
        if (this.donhangs[i].id == this.id) {
          // var dhang: donhang = {
          //   id: this.donhangs[i].id, kh: kh, trangthai: don.trangthai, thanhtoan: don.thanhtoan == null ? 0 : don.thanhtoan,
          //   tong: don.tong, nolai: don.tong - don.thanhtoan, ngaytao: this.donhangs[i].ngaytao, ngaygiao: don.ngaygiao, laixe: this.donhangs[i].laixe, xe: x
          // };
          this.donhangs[i].kh = kh;
          this.donhangs[i].trangthai = don.trangthai;
          this.donhangs[i].thanhtoan = don.thanhtoan == null ? 0 : don.thanhtoan;
          this.donhangs[i].tong = this.t;
          this.donhangs[i].nolai = this.t - don.thanhtoan;
          this.donhangs[i].ngaygiao = don.ngaygiao
          this.donhangs[i].xe = x;
          console.log("don hang", this.donhangs[i]);
          this.dataService.updateDh(this.donhangs[i]);
          this.chonxe = "Chọn xe giao hàng"
          console.log("bks", this.bks);

       
          console.log("chõne", this.chonxe);

        }
      };
      this.chonxe = "Chọn xe giao hàng"
      this.dataService.countState(this.dataService.donhangs);
console.log("count");
      // for (let j = 0; j < this.bks.length; j++) {
      //   var mh:mathang;
      //   mh = {tenhang: this.bks[j].mathang.tenhang, dvt: this.bks[j].mathang.dvt, dongia: this.bks[j].mathang.dongia};
      //   var it: bk = { id: this.id, mathang: mh , soluong: this.bks[j].soluong, thanhtien: this.bks[j].thanhtien };
      this.dataService.removeBks(this.id, this.bkremove);
      this.dataService.addBks(this.bks);
      console.log("add");

    }
  }
}



