import { Pipe } from '@angular/core';
import { PipeTransform } from '@angular/core/src/change_detection/pipe_transform';

@Pipe({
    name: 'reverse'
})
export class ReversePipe implements PipeTransform{
    
    transform (arr:any[]){
        return arr.slice().reverse();
    }
}


