import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule} from '@angular/router';
import { CommonModule} from '@angular/common';
import { FormsModule} from '@angular/forms';
import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { environment } from '../environments/environment';
import { AlertModule } from 'ngx-bootstrap';
import { HttpModule } from "@angular/http"
import { AppComponent } from './app.component';
import { NarbarComponent } from './narbar/narbar.component';
import { LeftmenuComponent } from './leftmenu/leftmenu.component';

import { DataService } from './leftmenu/share/data.service';
import { ReversePipe } from './leftmenu/share/reverse.pipe';
import { FilterbkPipe } from './leftmenu/share/filterbk.pipe';
import { DatePipe } from '@angular/common';
import { PagenotfoundComponent } from './pagenotfound/pagenotfound.component';
import { TaoDonHangComponent } from './tao-don-hang/tao-don-hang.component';
import { ViewDonhangComponent } from './view-donhang/view-donhang.component';
import { ViewDetailComponent } from './view-detail/view-detail.component';
import { SuaDonHangComponent } from './sua-don-hang/sua-don-hang.component';
@NgModule({
  declarations: [
    AppComponent,
    NarbarComponent,
    LeftmenuComponent,
    PagenotfoundComponent,
    TaoDonHangComponent,
    ViewDonhangComponent,
    ReversePipe,
    FilterbkPipe,
    ViewDetailComponent,
    SuaDonHangComponent,
  ],
  imports: [
    BrowserModule,
    HttpModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFirestoreModule,
    CommonModule,
    FormsModule,
    
    RouterModule.forRoot([
      { path: 'tao-don-hang/:title', component: TaoDonHangComponent },
      { path: 'xem-don-hang/:title/:state', component: ViewDonhangComponent },
      { path: 'xem-don-hang/Xem đơn hàng/:state/:id', component: ViewDetailComponent },
      { path: 'sua-don-hang/:title/:id', component: SuaDonHangComponent },
      { path: '', redirectTo: '/xem-don-hang/Xem đơn hàng/Tất cả', pathMatch: 'full' },
      { path: '**', component: PagenotfoundComponent }
    ]),
    AlertModule.forRoot()
  ],
  providers: [ DataService, ReversePipe, DatePipe],
  bootstrap: [AppComponent]
})
export class AppModule { }



