import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { DataService } from '../leftmenu/share/data.service';
import { ReversePipe } from '../leftmenu/share/reverse.pipe';
import { donhang } from '../models/donhang';
import { bk } from '../models/bk';
import { mathang } from '../models/mathang';

@Component({
  selector: 'app-view-donhang',
  templateUrl: './view-donhang.component.html',
  styleUrls: ['./view-donhang.component.css']
})
export class ViewDonhangComponent implements OnInit {
  state = "";
  donhangs: donhang[];
  bk: bk[];
  mathang: mathang[];
  mh: mathang;
  constructor(private route: ActivatedRoute, private dataService: DataService) {
    this.dataService.getDonhangs().subscribe(donhangs => {
      this.donhangs = donhangs;
     //console.log("viewdonhang",this.donhangs[0].ngaygiao.getDate().toString());
    });

    this.dataService.getBks().subscribe(bks => {
      this.bk = bks;      
     // console.log("viewdonhang-bk",this.bk);
    });
  }

  ngOnInit() {

    this.route.paramMap.subscribe((param: ParamMap) => {
      return this.state = param.get('state');
    });
console.log("state", this.state);
  }
 

  cancel(id:string){
    for (let i = 0; i < this.donhangs.length; i++) {
      if(this.donhangs[i].id === id) {
        this.donhangs[i].trangthai = "Đơn bị hủy";
        this.dataService.updateDh(this.donhangs[i]);
        this.dataService.countState(this.donhangs);
      }

    }

  }
}




