import { Component, OnInit } from '@angular/core';
import { DataService } from '../leftmenu/share/data.service';
import { DatePipe } from '@angular/common';
import { khachhang } from '../models/khachhang';
import { xe } from '../models/xe';
import { mathang } from '../models/mathang';
import { donhang } from '../models/donhang';
import { bk } from '../models/bk';

@Component({
  selector: 'app-tao-don-hang',
  templateUrl: './tao-don-hang.component.html',
  styleUrls: ['./tao-don-hang.component.css']
})
export class TaoDonHangComponent {

  items = [];
  item = { stt: 1, tenhang: "a", dvt: "v", soluong: 0, dongia: 0, thanhtien: 0 }
  i = 1;
  mathang: mathang[];
  lengthdh: number;
  xe: xe[];
  tong = 0;
  statebk = true;
  now: Date = new Date();
  constructor(private dataService: DataService) {
    this.dataService.getMathangs().subscribe(mh => {
      this.mathang = mh;
      console.log("mh", this.mathang);
    });

    this.dataService.getXes().subscribe(x => {
      this.xe = x;
      console.log("xes", this.xe);
    });
    this.dataService.getDonhangs().subscribe(d => {
      this.lengthdh = d.length;

    });


  }

  ngOninit() {
    console.log("xes", this.xe);
  }
  th = "Chọn mặt hàng";
  dt = "";
  sl: number = null;
  dg: number = null;
  chonxe = "Chọn xe giao hàng"
  addTemp = function (temp) {
    if (temp.tenhang != "" && temp.dvt != "" && temp.soluong != 0) {
      this.item.stt = this.items.length + 1;
      this.item.tenhang = temp.tenhang;
      this.item.dvt = temp.dvt;
      this.item.soluong = temp.soluong;
      this.item.dongia = temp.dongia;
      this.item.thanhtien = temp.dongia * temp.soluong;
      this.items.push(this.item);

      this.item = {};
      temp = {};
      console.log('items', this.items);
      this.th = "Chọn mặt hàng";
      this.dv = "";
      this.sl = null;
      this.dg = null;
    }

    this.tong = 0;
    for (let i = 0; i < this.items.length; i++) {
      this.tong = this.tong + this.items[i].thanhtien;
    }





  }

  adddon = function (don) {
    console.log(don);

    if (don.tenkh != "" && don.sdt != "" && don.diachi != "") {
      var i = this.lengthdh + 1;
      var kh: khachhang = { tenkh: don.tenkh, sdt: don.sdt, dc: don.diachi };
      var x: xe;
      for (let i = 0; i < this.xe.length; i++) {
        if (this.xe[i].bienso == don.xe) { x = this.xe[i]; }
      };

      var donhang: donhang = {
        id: "bk" + i, kh: kh, trangthai: "Chưa giao", thanhtoan: don.thanhtoan == null ? 0 : don.thanhtoan,
        tong: this.tong, nolai: this.tong - don.thanhtoan, ngaytao: this.now, ngaygiao: don.ngaygiao, laixe: "", xe: x
      };
      for (let i = 0; i < this.items.length; i++) {
        var mh: mathang;
        mh = { tenhang: this.items[i].tenhang, dvt: this.items[i].dvt, dongia: this.items[i].dongia };
        var it: bk = { id: donhang.id, mathang: mh, soluong: this.items[i].soluong, thanhtien: this.items[i].thanhtien };
        this.dataService.addBk(it);

      }
      this.dataService.addDonhang(donhang);
      this.chonxe = "Chọn xe giao hàng"
    }

    this.dataService.countState(this.dataService.donhangs);



  }
  updateBk(ind: number) {
    console.log(ind);
    this.statebk = false;
    this.th = this.items[ind].tenhang;
    this.dt = this.items[ind].dvt;
    this.sl = this.items[ind].soluong;
    this.dg = this.items[ind].dongia;
    this.items.splice(ind, 1);


  }

  removeBk(index) {
    //console.log(this.bks[index]);
    // var bktemp: bk = this.bks[index];

    this.items.splice(index, 1);

    this.tong = 0;
    for (let i = 0; i < this.items.length; i++) {
      this.tong = this.tong + this.items[i].thanhtien;
    }


    console.log("tong: ", this.tong);

  }

  //this.dataService.removeBk(bktemp);

}




